import React, {useEffect, useState} from 'react';
import Input from "../../UI/Input/Input";
import styles from "./styles.module.scss"
import classnames from "classnames";
import Multiselect from "react-widgets/Multiselect";
import Combobox from "react-widgets/Combobox";
import axios from "axios";
import {DropdownList} from "react-widgets/cjs";
import Button from "../../UI/Button/Button";
import {Checkbox, createTheme, FormControlLabel, ThemeProvider} from "@material-ui/core";


function Filter({setEvents, setLoading}) {
    const [regions, setRegions] = useState([])
    const [region, setRegion] = useState("")

    const [city, setCity] = useState("")
    const [cities, setCities] = useState("")

    const [levels, setLevels] = useState([])

    const [level, setLevel] = useState("")

    const [fields, setFields] = useState([])
    const [outfields, setOutfields] = useState([])

    const [name, setName] = useState("")

    const [online, setOnline] = useState(true)
    const [fcdo, setFcdo] = useState(true)

    const [ageStart, setAgeStart] = useState(0)
    const [ageEnd, setAgeEnd] = useState(250)

    useEffect(() => {
        axios.get("/api/regions")
            .then(response => response.data)
            .then(response => {
                setRegions(response)
                console.log(response)
            })
            .catch()
    }, [])


    useEffect(() => {
        axios.get("/api/cities")
            .then(response => response.data)
            .then(response => {
                setCities(response)
            })
            .catch()
    }, [])

    useEffect(() => {
        axios.get("/api/levels")
            .then(response => response.data)
            .then(response => {
                setLevels(response)
            })
            .catch()
    }, [])

    useEffect(() => {
        axios.get("/api/fields")
            .then(response => response.data)
            .then(response => {
                setFields(response)
            })
            .catch()
    }, [])

    const theme = createTheme({
        palette: {
            primary: {
                main: "#FE724C",
            },
        },
    });

    return (
        <aside className={classnames(styles.aside)}>
            <form className={classnames(styles.form)} onSubmit={event => {
                event.preventDefault();
                setLoading(false)
                axios.post("/api/events-by-filter", {
                    name: name,
                    level_id: level,
                    region_id: region,
                    city_id: city,
                    age_start: +ageStart,
                    age_end: +ageEnd,
                    fields_id: outfields.map(elem => elem.id),
                    online: online,
                    fcdo: fcdo
                })
                    .then(response => response.data)
                    .then(response => setEvents(response))
                    .then(r => setLoading(true))
            }}>
                <Input placeholder="" onInput={event => setName(event.target.value)}/>
                <DropdownList
                    placeholder=" "
                    data={levels}
                    dataKey="id"
                    textField="name"
                    onSelect={value => {
                        setLevel(value.id)
                    }}
                />
                <Combobox
                    hideCaret
                    hideEmptyPopup
                    placeholder=" "
                    data={regions}
                    dataKey="id"
                    textField="name"
                    onSelect={value => {
                        setRegion(value.id)
                    }}
                />
                <Combobox
                    hideCaret
                    hideEmptyPopup
                    placeholder=" "
                    data={cities}
                    dataKey="id"
                    textField="name"
                    onSelect={value => {
                        setCity(value.id)
                    }}
                />
                <Multiselect placeholder=""
                             hideCaret
                             hideEmptyPopup
                             data={fields}
                             value={outfields}
                             dataKey="id"
                             textField="name"
                             onChange={value => {
                                 setOutfields(value)
                                 console.log(outfields)
                             }}/>

                <div className={classnames(styles.ages)}>
                    <Input placeholder="" onInput={event => setAgeStart(event.target.value)} type="number" min={0}
                           max={90}/>
                    <Input placeholder="" onInput={event => setAgeEnd(event.target.value)} type="number" min={0}
                           max={90}/>
                </div>
                <div className={classnames(styles.checks)}>
                    <ThemeProvider theme={theme}>

                        <FormControlLabel label=''
                                          control={<Checkbox
                                              checked={online}
                                              onChange={() => {
                                                  setOnline(!online)
                                                  console.log(online)
                                              }}
                                              color="primary"
                                          />}
                        />
                        <FormControlLabel label=''
                                          control={<Checkbox
                                              checked={fcdo}
                                              onChange={() => {
                                                  setFcdo(!fcdo)
                                                  console.log(online)
                                              }}
                                              color="primary"
                                          />}
                        />

                    </ThemeProvider>
                </div>
                <div className={classnames(styles.buttons)}>
                    <Button type="submit"></Button>
                    <Button onClick={event => {
                        event.preventDefault()
                        setLoading(false)
                        axios.get("/api/events")
                            .then(response => response.data)
                            .then(response => {
                                setEvents(response)
                                setLoading(true)
                            })
                            .catch(err => console.log(err))
                    }}></Button>
                </div>


            </form>

        </aside>
    );
}

export default Filter;