import React, {useState} from 'react';
import RoundButton from "../../UI/RoundButton/RoundButton";
import {ArrowBackRounded} from "@material-ui/icons";
import styles from "./styles.module.scss"
import classnames from "classnames";
import Input from "../../UI/Input/Input";
import {NavLink, useNavigate} from "react-router-dom";
import Button from "../../UI/Button/Button";
import axios from "axios";

function LoginPage() {

    const navigate = useNavigate();
    const [password, setPassword] = useState("");
    const [login, setLogin] = useState("")



    return (
        <main className={classnames(styles.main)}>
            <section className={classnames(styles.section)}>
                <div><RoundButton onClick={() => {
                    navigate(-1)
                }}><ArrowBackRounded/></RoundButton></div>
                <h1>  </h1>
                <form onSubmit={(event) => {
                    event.preventDefault();
                    axios.post("/api/login", {login: login, password: password})
                }}>
                    <Input name={"email"} onInput={(event) => {setLogin(event.target.value)}} placeholder={"Email   "}/>
                    <Input name={"password"} onInput={(event) => {setPassword(event.target.value)}} placeholder={""}/>
                    <Button type={"submit"}></Button>
                    <p>   ? <NavLink className={classnames(styles.link)} to="/register"></NavLink></p>
                </form>

            </section>
        </main>
    );
}

export default LoginPage;