import React, {useEffect, useState} from 'react';
import Image from "../../components/Image/Image";
import axios from "axios";
import {PulseLoader} from "react-spinners";
import classnames from "classnames"
import styles from "./styles.module.scss"

function Organization({ Id }) {

    const [data, setData] = useState({})

    useEffect(() => {
        axios.get(`/api/organization/${Id}`)
            .then(response => response.data)
            .then(response => setData(response))
            .catch(() => {})
    }, [])

    if (data) {
        return (
            <div className={classnames(styles.logo_wrap)}>
                <div>
                    <h2 className={classnames(styles.accent)}>{data.full_name}</h2>
                    <h2><span className={classnames(styles.accent)}>: </span>{data.address}</h2>
                    <h2><span className={classnames(styles.accent)}>: </span>{data.tax_number}</h2>
                    <h2><span className={classnames(styles.accent)}>  : </span>{data.email}</h2>
                    <h2>: <span className={classnames(data.confirmed ? styles.good : styles.bad)}>{data.confirmed ? "" : " "}</span></h2>
                </div>
                <Image image={data.logo}/>
            </div>
        );
    }

    else {
        return <PulseLoader color="#FE724C"/>
    }


}

export default Organization;