import React, {useEffect, useState} from 'react';
import EventCard from "../../widgets/EventCard/EventCard";
import styles from "./styles.module.scss"
import classnames from "classnames";
import axios from "axios";
import Filter from "../../widgets/Filter/Filter";
import {PulseLoader} from "react-spinners";


function MainPage(props) {

    const [events, setEvents] = useState([]);
    const [loading, setLoading] = useState(false)

    useEffect(() => {
            setLoading(false)
            axios.get("/api/events")
                .then(response => response.data)
                .then(response => {
                    setEvents(response)
                    setLoading(true)
                })
                .catch(err => console.log(err))
        },
        []
    )

    return (
        <main className={classnames(styles.page)}>
            <section className={styles.event_section}>
                {loading ?
                    events.map(data => <EventCard
                        id={data.id}
                        key={data.id}
                        image={data.banner}
                        title={data.name}
                        ages={data.ages}
                        isOnline={data.online}
                        status={data.status}
                        date={data.date}
                        region={data.region}
                        level={data.level}
                        tags={data.fields}
                    />) : <PulseLoader color="#FE724C"/>
                }
                {}
            </section>
            <section className={classnames(styles.aside_section)}>
                <Filter setEvents={setEvents} setLoading={setLoading}></Filter>
            </section>
        </main>
    );
}

export default MainPage;