import React from 'react';
import classnames from "classnames";
import styles from "./styles.module.scss"
import Tag from "../../UI/Tag/Tag";
import OutlinedText from "../../UI/OutlinedText/OutlinedText";

function EventInfo({title, date, region, level, ages, isOnline, tags}) {
    return (
        <>
            <h3 className={classnames(styles.title)}>{title}</h3>
            <p className={classnames(styles.ages)}>{date}</p>
            <p className={classnames(styles.list)}>
                <span>
                  &#8226; {region}
                </span>
                <span>
                   &#8226; {level}
                </span>
            </p>
            <div className={classnames(styles.info)}>
                <OutlinedText>{ages} </OutlinedText>
                <OutlinedText>{isOnline ? "" : ""}</OutlinedText>
            </div>
            <div className={classnames(styles.tag_list)}>
                {tags ? tags.map(tag => <Tag>{tag}</Tag>
                )  : " "}
            </div>
        </>
    );
}

export default EventInfo;