import React, {useEffect, useState} from 'react';
import classnames from "classnames";
import styles from "./styles.module.scss"
import axios from "axios";
import Organization from "../../widgets/Organization/Organization";
import OrganizationForm from "../../widgets/OrganizationForm/OrganizationForm";
import IconButton from "../../UI/IconButton/IconButton";
import {ExitToApp} from "@material-ui/icons";

function ProfilePage() {

    const [data, setData] = useState({})

    useEffect(() => {
        axios.get("/api/is_in_organization")
            .then(response => response.data)
            .then(response => setData(response))
            .catch(() => {})
    }, [])

    return (
        <main className={classnames(styles.main)}>
            <section className={classnames(styles.section)}>
                <h1></h1>
                {
                    data.status ? <Organization Id={data.organization_id} /> : <OrganizationForm />
                }
                <IconButton onClick={() => {
                    axios.post("/api/logout")
                }} icon={<ExitToApp />}></IconButton>
            </section>

        </main>
    );
}

export default ProfilePage;