import React from 'react';
import styles from './styles.module.scss'
import classnames from "classnames";
function Input({placeholder, type, onInput, name, min, max}) {
    return (
        <input min={min} max={max} placeholder={placeholder} name={name} type={type} onInput={onInput} className={classnames(styles.input)}/>
    );
}

export default Input;