import React, {useEffect, useState} from 'react';
import {NavLink, useParams} from "react-router-dom";
import axios from "axios";
import classnames from "classnames";
import styles from "./styles.module.scss"
import Image from "../../components/Image/Image";
import EventInfo from "../../components/EventInfo/EventInfo";
import {Description} from "@material-ui/icons";


function EventPage() {
    const {eventId} = useParams();
    const [data, setData] = useState({})

    useEffect(() => {
        axios.get(`/api/event/${eventId}`)
            .then(response => response.data)
            .then(response => {
                setData(response)
            })
            .catch(() => {
            })
    }, [eventId,])
    return (
        <main className={classnames(styles.main)} onClick={() => {
            console.log(data)
        }}>
            <section className={classnames(styles.section)}>
                <article className={classnames(styles.top)}>
                    <div className={classnames(styles.image_wrap)}>
                        <Image image={data.banner} />
                    </div>
                    <article className={classnames(styles.info)}>
                        <EventInfo title={data.name}
                                   date={data.date}
                                   region={data.region}
                                   level={data.level}
                                   tags={data.fields}
                                   ages={data.ages}
                                   status={data.status}
                                   isOnline={data.online}/>
                    </article>
                </article>
                <text className={classnames(styles.text)}>{data.description}</text>
                <h2></h2>
                <section className={classnames(styles.file)}>
                    {
                        data.hasOwnProperty("docs") ?
                            data.docs.map(elem =>
                                <a key={<elem className="data"></elem>} href={axios.defaults.baseURL + elem.data}><Description/><h2> {elem.name}</h2></a>
                            ) : <h1>Loading</h1>
                    }
                </section>
                <h2></h2>
                <div className={classnames(styles.logo_wrap)}>
                    <NavLink to={`/organization/${data.organization_id}`}><Image image={data.organization_logo} /></NavLink>
                </div>


            </section>
        </main>
    );
}

export default EventPage;