import React from 'react';
import classnames from "classnames";
import styles from "./styles.module.scss"
import {useNavigate} from "react-router-dom";
import Image from "../../components/Image/Image";
import EventInfo from "../../components/EventInfo/EventInfo";


function EventCard({id, image, title, date, region, level, tags, ages, status, isOnline}) {
    const navigate = useNavigate();
    return (
        <section className={classnames(styles.card)} onClick={event => navigate(`/event/${id}`)
        }>
            <Image image={image}/>
            <EventInfo
                title={title}
                date={date}
                region={region}
                level={level}
                tags={tags}
                ages={ages}
                status={status}
                isOnline={isOnline}/>

        </section>
    );
}

export default EventCard;