import React, {useEffect, useState} from 'react';
import classnames from "classnames"
import styles from "./styles.module.scss"
import Input from "../../UI/Input/Input";
import Combobox from "react-widgets/Combobox";
import axios from "axios";
import FileInput from "../FileInput/FileInput";
import Button from "../../UI/Button/Button";

function OrganizationForm(props) {

    const [regions, setRegions] = useState([])
    const [region, setRegion] = useState("")

    const [city, setCity] = useState("")
    const [cities, setCities] = useState("")
    const [shortName, setShortName] = useState("")
    const [name, setName] = useState("")
    const [address, setAddress] = useState("")
    const [tax, setTax] = useState("")
    const [email, setEmail] = useState("")
    const [url, setUrl] = useState("")
    const [file, setFile] = useState()


    useEffect(() => {
        axios.get("/api/regions")
            .then(response => response.data)
            .then(response => {
                setRegions(response)
                console.log(response)
            })
            .catch()
    }, [])


    useEffect(() => {
        axios.get("/api/cities")
            .then(response => response.data)
            .then(response => {
                setCities(response)
            })
            .catch()
    }, [])


    return (
        <form action={axios.defaults.baseURL + "/api/go"} onClick={() => {
            console.log(file)
        }} className={classnames(styles.form)}>
            <Input onInput={event => setShortName(event.target.value)} placeholder=" "/>
            <Input onInput={event => setName(event.target.value)} placeholder=" "/>
            <Input onInput={event => setAddress(event.target.value)} placeholder=""/>
            <Input onInput={event => setTax(event.target.value)} placeholder=""/>
            <Input onInput={event => setEmail(event.target.value)} placeholder="Email"/>
            <Input onInput={event => setUrl(event.target.value)} placeholder="   "/>
            <Combobox
                hideCaret
                hideEmptyPopup
                placeholder=" "
                data={regions}
                dataKey="id"
                textField="name"
                onSelect={value => {
                    setRegion(value.id)
                }}
            />
            <Combobox
                hideCaret
                hideEmptyPopup
                placeholder=" "
                data={cities}
                dataKey="id"
                textField="name"
                onSelect={value => {
                    setCity(value.id)
                }}
            />
            <FileInput onChange={event => {
                if (event.target.files[0]) {
                    setFile(event.target.files[0])
                }
                console.log(file)
            }}/>

            <Button onClick={event => {
                event.preventDefault()
                let bodyFormData = new FormData();
                bodyFormData.append('logo', file)
                bodyFormData.append('city_id', city)
                bodyFormData.append('short_name', shortName)
                bodyFormData.append('full_name', name)
                bodyFormData.append('tax_number', tax)
                bodyFormData.append('address', address)
                bodyFormData.append('email', email)
                bodyFormData.append('site', url)
                bodyFormData.append('region_id', region)
                axios({
                    method: "post",
                    url: `${axios.defaults.baseURL}/api/organization`,
                    data: bodyFormData,
                    headers: {"Content-Type": "multipart/form-data"}
                })
            }}></Button>

        </form>
    );
}

export default OrganizationForm;